*{
  padding: 0;
  margin: 0;
}
.homeCanvas{
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    -webkit-filter: blur(10px);
            filter: blur(10px);
    position: absolute;
}
.homeCanvasContainer{
    width: 100%;
    background-color: rgba(0, 0, 0, 0.046);
    z-index: 3;
    position: relative;
    height: 100vh;
    display: flex;
    color: white;
}
.homeScrollBtn{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    border-radius: 100%;
    left: 50%;
    top: 500px;
    padding: 0.5vmax;
    -webkit-animation: scrollPop 0.5s ease-in infinite alternate;
            animation: scrollPop 0.5s ease-in infinite alternate;
}
.homeScrollBtn>svg{
    font-size: 3vmax;
}
.homeContainer{
    background-color: white;
    padding-top: 4vmax;
    border-radius: 0 0 150px 150px;
    border-bottom: 200px solid white;
    position: relative;
    z-index: 1;
    transition: 0.1s ease-in-out;
}

.homeContainer:hover{
    box-shadow: 8px 4px 8px 4px rgba(242, 241, 241, 0.13);
}

.homeContainer >h3{
    text-align: center;
    color: rgba(22, 20, 20, 0.904);
    padding: 4vmax;
}

.homeSkills{
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
    background-color: hsl(219, 48%, 8%);
    height: 1500px;
    position: relative;
    padding: 200px;
    margin-bottom: -200px;
    box-sizing: border-box;
    -webkit-perspective: 450;
            perspective: 450;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    overflow-x: hidden;
}

.homeSkills>h3{
    color: white;
    text-align: center;
    margin-bottom: 4vmax;
    font-size: 4vmax;
}

.homeCubeSkills{
    height: 150px;
    width: 150px;
    -webkit-animation: cubeRotation 20s linear infinite alternate;
            animation: cubeRotation 20s linear infinite alternate;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.homeCubeSkillsFaces{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 10px rgb(0,0,0);
    position: absolute;
    width: inherit;
    height: inherit;
}
.homeCubeSkillsFaces >img{
    width: inherit;
    height: inherit;
    opacity: 0.7;
}

.homeCubeSkillsFace1{
    -webkit-transform: translate3d(0,0,80px);
            transform: translate3d(0,0,80px);
}
.homeCubeSkillsFace2{
    -webkit-transform: rotateY(180deg) translate3d(0,0,80px);
            transform: rotateY(180deg) translate3d(0,0,80px);
}
.homeCubeSkillsFace3{
    -webkit-transform: rotateY(-90deg) translate3d(0,0,80px);
            transform: rotateY(-90deg) translate3d(0,0,80px);
}
.homeCubeSkillsFace4{
    -webkit-transform: rotateY(90deg) translate3d(0,0,80px);
            transform: rotateY(90deg) translate3d(0,0,80px);
}
.homeCubeSkillsFace5{
    -webkit-transform: rotateX(90deg) translate3d(0,0,80px);
            transform: rotateX(90deg) translate3d(0,0,80px);
}
.homeCubeSkillsFace6{
    -webkit-transform: rotateX(-90deg) translate3d(0,0,80px);
            transform: rotateX(-90deg) translate3d(0,0,80px);
}
.cubeShadow{
    width: 300px;
    height: 40px;
    background-color: rgba(255, 255, 255, 0.171);
    border-radius: 50%;
    -webkit-animation: cubeShadow 1s ease-in-out infinite alternate;
            animation: cubeShadow 1s ease-in-out infinite alternate;
    -webkit-filter: blur(3px);
            filter: blur(3px);
}
.homeSkillsBox{
    background-color: rgb(37, 26, 94);
    padding: 0 2vmax;
    position: absolute;
    display: flex;
    flex-direction: column;
    right: -300px;
    -webkit-animation-duration:  1s;
            animation-duration:  1s; 
    -webkit-animation-timing-function: linear; 
            animation-timing-function: linear;
    -webkit-animation-fill-mode:  forwards;
            animation-fill-mode:  forwards;
}
.homeSkillsBox>svg{
    font-size: 5vmax;
    color: aliceblue;
    transition: all 0.5s;
    margin: 10px;
}
.homeSkillsBox>svg:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.homeCodingPlatforms{
    background-color: white;
    padding-top: 4vmax;
    -webkit-transform: translateY(-200px);
            transform: translateY(-200px);
    border-radius: 150px 150px 0 0;
    position: relative;
    margin-bottom: -200px;
    box-sizing: border-box;
    min-height: 900px;
}
.homeCodingPlatforms>h3{
    text-align: center;
}

.platformWrapper{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 2vmax;
    width: 80%;
    margin: auto;
}

@-webkit-keyframes cubeRotation{
    to {
        -webkit-transform: rotateY(360deg) rotateX(480deg) rotateZ(720deg);
                transform: rotateY(360deg) rotateX(480deg) rotateZ(720deg);
    }
}

@keyframes cubeRotation{
    to {
        -webkit-transform: rotateY(360deg) rotateX(480deg) rotateZ(720deg);
                transform: rotateY(360deg) rotateX(480deg) rotateZ(720deg);
    }
}
@-webkit-keyframes cubeShadow{
    to {
        -webkit-transform: scale(1.2) translateY(-10px);
                transform: scale(1.2) translateY(-10px);
    }
}
@keyframes cubeShadow{
    to {
        -webkit-transform: scale(1.2) translateY(-10px);
                transform: scale(1.2) translateY(-10px);
    }
}

@-webkit-keyframes homeSkillsBoxAnimationOn{
    from{
        right: -300px;
    }
    to{
        right: 0px;
    }
}

@keyframes homeSkillsBoxAnimationOn{
    from{
        right: -300px;
    }
    to{
        right: 0px;
    }
}
@-webkit-keyframes homeSkillsBoxAnimationOff{
    from{
        right: 0px;
    }
    to{
        right: -300px;
    }
}
@keyframes homeSkillsBoxAnimationOff{
    from{
        right: 0px;
    }
    to{
        right: -300px;
    }
}

@-webkit-keyframes scrollPop {
    to{
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
}

@keyframes scrollPop {
    to{
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
}
.codingPlatform{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 300px;
    transition: all 0.5s;
    padding: 1vmax;
    
}
.codingPlatform>img{
    width: inherit;
}
a img{
    margin: 50px 50px 0 0;
}
a img:hover{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        box-shadow: 0 0 10px rgba(0,0,0,0.329);
    }
@media screen and (max-width: 768px){
    .codingPlatform{
        width: 200px;
        flex-wrap: wrap;
    }
}
.footer{
    background-color: rgb(6, 11, 23);
    padding: 3vmax;
    display: grid;
    grid-template-columns: 1fr 200px;
}
.footer>div>h5{
    color: white;
    border-bottom: 1px solid white;
    width: 120px;
    margin-bottom: 20px;
    padding: 10px 0;
}
.footer>div>p{
    color: aliceblue;
    width: 60%;
}
.footer>div>h6{
    color: aliceblue;
    padding: 10px 0;
}
.footer >div:last-child{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.75rem;
}
.footer>div>a{
    color: aliceblue;
    transition: all 0.5s;
}

.footerContactBtn{
    background-color: aliceblue;
    padding: 5px;
    width: 120px;
    text-decoration: none;
    font-size: 2rem;
    text-align: center;
    display: block;
    font-weight: bold;
    transition: ease-in-out 0.5s;
    margin: 2vmax 0;
}
.footerContactBtn > p{
    color: black;
}
.footerContactBtn:hover{
    background-color: transparent;
    border: 1px solid aliceblue;
}
.footerContactBtn > p:hover{
    color: aliceblue;
}

.footer>div>a:nth-child(2):hover{
    color: rgb(43, 116, 226);
}
.footer>div>a:nth-child(3):hover{
    color: rgb(92, 43, 226);
}

.footer>div>a:nth-child(5):hover{
    color: rgb(195, 47, 240);
}

.footer>div>a:nth-child(4):hover{
    color: rgb(156, 206, 250);
}

@media screen and (max-width:768px) {
    .footer{
        grid-template-columns: 100%;
    }
    .footer>div>:first-child{
        width: 100%;
    }
    .footer >div:last-child{
        width: 100%;
        flex-direction: row;
    }
    .footer>div:last-child a{
        display: block;
        padding: 5px;
    }
    .footer>div>a:nth-child(2){
        color: rgb(43, 116, 226);
    }
    .footer>div>a:nth-child(3){
        color: rgb(92, 43, 226);
    }
    
    .footer>div>a:nth-child(5){
        color: rgb(195, 47, 240);
    }
    
    .footer>div>a:nth-child(4){
        color: rgb(156, 206, 250);
    }
    
}
.about{
    background-color: hsl(219, 48%, 8%);
    min-height: 100vh;
    display: flex;
}

.aboutContainer{
    background-color: aliceblue;
    width: 60%;
    height: 70%;
    position: absolute;
    right: 0;
    padding-right: 0;
    display: flex;
    justify-content: flex-end;
    padding: 2vmax;
    box-sizing: border-box;
}

.aboutContainer>p{
    text-align: right;
    width: 30%;
    margin-right: 0;
    letter-spacing: 5px;
    word-spacing: 5px;
    line-height: 60px;
    font-size: 2vmax;
    color: rgb(30, 9, 49);
}

.aboutContainer2{
    width: 60%;
    background-color: aliceblue;
    box-shadow: 0 0 15px rgba(0,0,0,0.281);
    position: absolute;
    bottom: 0;
    box-sizing: border-box;
    padding: 2rem;
    display: flex;
    -webkit-transform: translateX(-100vw) translateY(-10%) rotateZ(360deg);
            transform: translateX(-100vw) translateY(-10%) rotateZ(360deg);
    -webkit-animation: aboutAnimation 1s ease-in forwards;
            animation: aboutAnimation 1s ease-in forwards;
}

.aboutContainer2>div{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    color: rgb(18, 16, 16);
    box-sizing: border-box;
}

.aboutContainer2>div:first-child{
    align-items: center;
}

.aboutContainer2>div:last-child{
    height: 100%;
}
.aboutAvatar{
    width: 250px;
    height: 300px;
    transition: all 1s;
    border-radius: 50%;
}
.aboutAvatar:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 0 10px rgba(0,0,0,0.289);
}

@media screen and (max-width: 768px) {
    .about{
        align-items: flex-end;
    }
    .aboutContainer{
        width: 100%;
        display: none;
    }
    .aboutContainer2{
        width: 100%;
        position: relative;
        flex-direction: column;
        -webkit-animation: aboutAnimationPhone 1s ease-in forwards;
                animation: aboutAnimationPhone 1s ease-in forwards;
    }
    .aboutContainer2>div:last-child>p{
        text-align: center !important;
    }
    .aboutAvatar{
        width: 120px;
        height: 150px;
        -webkit-transform: translateY(-60%);
                transform: translateY(-60%);
    }
}



@-webkit-keyframes aboutAnimation {
    to{
        -webkit-transform: translateX(20%);
                transform: translateX(20%);
    }
    
}



@keyframes aboutAnimation {
    to{
        -webkit-transform: translateX(20%);
                transform: translateX(20%);
    }
    
}

@-webkit-keyframes aboutAnimationPhone {
    to{
        -webkit-transform: translateX(0) translateY(0);
                transform: translateX(0) translateY(0);
    }
}

@keyframes aboutAnimationPhone {
    to{
        -webkit-transform: translateX(0) translateY(0);
                transform: translateX(0) translateY(0);
    }
}

.projects{
    background-color: hsl(219, 48%, 8%);
    min-height: 100vh;
    padding-top: 15vmax;

}

.projects>h3{
    color: aliceblue;
    text-align: center;
    position: absolute;
    background-color: blueviolet;
    padding: 2vmax 4vmax;
    display: flex;
    border-radius: 100px;
    font-size: 1.5rem;
    align-items: center;
    -webkit-transform: translate(0%, -100vh);
            transform: translate(0%, -100vh);
    -webkit-animation: projectHeadingAnimation 1s ease-in-out forwards;
            animation: projectHeadingAnimation 1s ease-in-out forwards;
}
.projectWrapper{
    border-top: 100px solid aliceblue;
    border-radius: 100px 100px 0 0;
    display: flex;
    background-color: aliceblue;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 5vmax;
    padding-bottom: 10vmax;
    overflow-x: hidden;
}

.projectCard{
    text-decoration: none;
    width: 80%;
    margin: 2vmax;
    color: black;
    border-radius: 10px;
    display: flex;
    -webkit-transform: translateX(-100vw);
            transform: translateX(-100vw);
    -webkit-animation: projectCardAnimation 1s ease-in-out forwards 0.5s;
            animation: projectCardAnimation 1s ease-in-out forwards 0.5s;
}

.projectCard:nth-child(even){
    -webkit-transform: translateX(100vw);
            transform: translateX(100vw);
    flex-direction: row-reverse;
    -webkit-animation: projectCardAnimation 1s ease-in-out forwards 1s;
            animation: projectCardAnimation 1s ease-in-out forwards 1s;
}

.projectCard>div{
    width: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    align-items: center;
    justify-content: space-between;
    transition: all 0.5s;
}

.projectCard>div:first-child::after{
    content: "VIEW DEMO";
    height: 100%;
    width: 50%;
    padding: 5px 0;
    font-size: 1.7rem;
    font-weight: 300;
    font-family: "Roboto";
    align-items: center;
    justify-content: center;
    background-color: rgb(55, 19, 88);
    color: aliceblue;
    text-align: center;
}

.projectCard > div:first-child >h5{
    padding: 20px;
    
}
.projectCard > div:last-child{
    padding: 30px;
}
.projectCard > div:last-child>h4{
    text-align: center;
    font-size: 28px;
}
.projectCard > div:last-child>p{
    letter-spacing: 3px;
    word-spacing: 5px;
    text-align: right;
    font-size: 14px;
}
.projectCard:nth-child(even) div p{
    text-align: left;
}
.projectCard > div:last-child>h6{
    font-weight: bold;
    font-family: 'Ubuntu Mono', monospace;
}
.projectCard>div>img{
    width: inherit;
}

@-webkit-keyframes projectHeadingAnimation {
    to{
        -webkit-transform: translate(40vw, -50%);
                transform: translate(40vw, -50%);
    }
}

@keyframes projectHeadingAnimation {
    to{
        -webkit-transform: translate(40vw, -50%);
                transform: translate(40vw, -50%);
    }
}


@-webkit-keyframes  projectCardAnimation{
    to{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}


@keyframes  projectCardAnimation{
    to{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@media screen and (max-width:600px) {
    .projectCard{
        flex-direction: column;
        align-items: center;
    }
    .projectCard:nth-child(even){
        flex-direction: column;
    }
    .projectWrapper{
        padding: 2vmax;
    }
    .projectCard>div>img{
        margin: 0;
    }
    .projectCard > div:last-child>p{
        text-align: justify;
    }
    @-webkit-keyframes projectHeadingAnimation {
        to{
            -webkit-transform: translate(55%, -50%);
                    transform: translate(55%, -50%);
        }
    }
    @keyframes projectHeadingAnimation {
        to{
            -webkit-transform: translate(55%, -50%);
                    transform: translate(55%, -50%);
        }
    }
    
}
.contact{
    background-color: rgb(15, 2, 30);
    width: 100%;
    height: 100vh;
}

.contactRightBar{
    background-image: linear-gradient(to left,blueviolet,aliceblue);
    height: inherit;
    width: 30%;
    -webkit-transform: translateY(-100vw);
            transform: translateY(-100vw);
    -webkit-animation: contactForm 1s linear forwards 0.3s;
            animation: contactForm 1s linear forwards 0.3s;
    position: absolute;
    right: 0;
}

.contactContainer{
    width: 70%;
    height: 80%;
    position: absolute;
    background-color: aliceblue;
    -webkit-transform: translate(15%, 15%);
            transform: translate(15%, 15%);
    border-radius: 100px 0 0 100px;
    display: flex;
    justify-content: center;
    box-shadow: 0 0 10px rgba(152, 78, 236, 0.219);
    -webkit-transform: translateX(-100vw) translateY(20%);
            transform: translateX(-100vw) translateY(20%);
    -webkit-animation: contactTransition 1s linear forwards;
            animation: contactTransition 1s linear forwards;
}
.contactContainerForm h4{
    text-align: center;
    color: rgb(104, 32, 172);
}
.contactContainerForm{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 60%;
}

.contactContainerForm input,
.contactContainerForm textarea{
    outline: none;
    border: none;
    border-bottom: 1px solid rgb(104, 32, 172);
    padding: 10px;
    font-size: 1rem;
    font-family: "Roboto";
    background-color: transparent;
    
}
.contactContainerForm input{
    -webkit-transform: translateY(-100vw);
            transform: translateY(-100vw);
}

.contactContainerForm >button{
    background-color: rgb(104, 32, 172);
    transition: all 0.5s;
}
.contactContainerForm input{
    -webkit-animation: contactFormInput 2s ease-in-out forwards 0.8s;
            animation: contactFormInput 2s ease-in-out forwards 0.8s; 
}
.contactContainerForm textarea{
    opacity: 0;
    -webkit-animation: contactFormBtnTransition 2s ease-in-out forwards 2.8s;
            animation: contactFormBtnTransition 2s ease-in-out forwards 2.8s; 
}
.contactContainerForm >button{
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-animation: contactFormMessage 2s ease-in-out forwards 4s;
            animation: contactFormMessage 2s ease-in-out forwards 4s;
    -webkit-animation-iteration-count: 5;
            animation-iteration-count: 5; 
}
.contactContainerForm >button:hover{
    background-color: aliceblue;
    border: 1px solid rgb(104, 32, 172);
    color: blueviolet;
}

@-webkit-keyframes contactTransition {
    to{
        -webkit-transform: translateX(20%) translateY(20%);
                transform: translateX(20%) translateY(20%);
    }
}

@keyframes contactTransition {
    to{
        -webkit-transform: translateX(20%) translateY(20%);
                transform: translateX(20%) translateY(20%);
    }
}

@-webkit-keyframes contactForm {
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes contactForm {
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes contactFormInput {
    to{
        -webkit-transform: translateY(0) rotate(360deg);
                transform: translateY(0) rotate(360deg);
    }
}

@keyframes contactFormInput {
    to{
        -webkit-transform: translateY(0) rotate(360deg);
                transform: translateY(0) rotate(360deg);
    }
}

@-webkit-keyframes contactFormMessage {
    30% { -webkit-transform: scale(1.2); transform: scale(1.2); }
    40%, 60% { -webkit-transform: rotate(-20deg) scale(1.2); transform: rotate(-20deg) scale(1.2); }
    50% { -webkit-transform: rotate(20deg) scale(1.2); transform: rotate(20deg) scale(1.2); }
    70% { -webkit-transform: rotate(0deg) scale(1.2); transform: rotate(0deg) scale(1.2); }
    100% { -webkit-transform: scale(1); transform: scale(1); }
}

@keyframes contactFormMessage {
    30% { -webkit-transform: scale(1.2); transform: scale(1.2); }
    40%, 60% { -webkit-transform: rotate(-20deg) scale(1.2); transform: rotate(-20deg) scale(1.2); }
    50% { -webkit-transform: rotate(20deg) scale(1.2); transform: rotate(20deg) scale(1.2); }
    70% { -webkit-transform: rotate(0deg) scale(1.2); transform: rotate(0deg) scale(1.2); }
    100% { -webkit-transform: scale(1); transform: scale(1); }
}
@-webkit-keyframes contactFormBtnTransition {
    from { opacity: 0; }
    to { opacity: 1; }
    
}
@keyframes contactFormBtnTransition {
    from { opacity: 0; }
    to { opacity: 1; }
    
}
.login{
    background-color: hsl(219, 48%, 8%);
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginContainer{
    width: 50%;
    border-radius: 5px;
    background-color: aliceblue;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.219);
}

.loginForm{
    background-color: aliceblue;
    border-radius: 5px;
    height: 70vh;
    padding: 10px;
}
.loginForm h4{
    display: flex;
    margin: 2vmax;
    justify-content: center;
    -webkit-transform: translateY(-100vw);
            transform: translateY(-100vw);
    -webkit-animation: contactForm 1s linear forwards;
            animation: contactForm 1s linear forwards;
    word-spacing: 5px;
    letter-spacing: 15px;
}
.login h4 p{
    transition: all 0.5s;
    cursor: default;
}

.login h4 p:hover{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}

.loginForm div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2vmax 8vmax;
    box-sizing: border-box;
}
.loginForm input{
    padding: 15px;
    margin: 15px;
    outline: none;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid blueviolet;
}

.loginForm button{
    background-color: rgb(104, 32, 172);
    transition: all 0.5s;
}
.loginForm button:hover{
    border: 1px solid rgb(104, 32, 172);
    color: aliceblue;
    background-color: rgb(49, 9, 87);
    box-shadow: 0 0 10px rgba(0,0 ,0 ,0.219);
}

@media screen and (max-width:768px) {
    .loginContainer{
        width: 90%;
        border-radius: 0;
    }
    .loginForm{
